import React, { Component } from 'react';
import bridge from '@vkontakte/vk-bridge';
import {
	AdaptivityProvider,
	//   ConfigProvider,
	//   useAdaptivity,
	AppRoot,
	SplitLayout,
	SplitCol,
	//ViewWidth,
	View,
	Panel,
	PanelHeader,
	Header,
	Div,
	Group,
	Button,
	HorizontalScroll,
	//SimpleCell,
} from "@vkontakte/vkui";
import "@vkontakte/vkui/dist/vkui.css";



class App extends Component {

	constructor(props) {
		super(props);
		this.buttons = []
		for (let i = 0; i < 8; i++) {
			this.buttons[i] = false;
		}
		this.curbtn = 0
		this.timer = null

		this.btn0 = this.btn0.bind(this);
		this.btn1 = this.btn1.bind(this);
		this.btn2 = this.btn2.bind(this);
		this.btn3 = this.btn3.bind(this);
		this.btn4 = this.btn4.bind(this);
		this.btn5 = this.btn5.bind(this);
		this.btn6 = this.btn6.bind(this);
		this.btn7 = this.btn7.bind(this);
		this.FlashlightInteract = this.FlashlightInteract.bind(this);
	}

	FlashlightInteract() {
		var Light
		if (this.buttons[this.curbtn] == false) {
			Light = 0
		} else {
			Light = 1
		}
		console.log(this.curbtn)
		console.log(Light)
		bridge
			.send("VKWebAppFlashSetLevel", { "level": Light })
			.then(data => {
				// Обработка события в случае успеха
				console.log(data.result)
				this.curbtn++
				if (this.curbtn > 7) this.curbtn = 0
			})
			.catch(error => {
				console.log("Мне так плёха(")
				// Обработка события в случае ошибки
			});
	}

	btn0() {
		this.buttons[0] = !this.buttons[0]
	}
	btn1() {
		this.buttons[1] = !this.buttons[1]
	}
	btn2() {
		this.buttons[2] = !this.buttons[2]
	}
	btn3() {
		this.buttons[3] = !this.buttons[3]
	}
	btn4() {
		this.buttons[4] = !this.buttons[4]
	}
	btn5() {
		this.buttons[5] = !this.buttons[5]
	}
	btn6() {
		this.buttons[6] = !this.buttons[6]
	}
	btn7() {
		this.buttons[7] = !this.buttons[7]
	}


	render() {
		if (this.timer == null) {
			this.timer = setInterval(this.FlashlightInteract, 1000);
		}

		return (
			<AdaptivityProvider>
				<AppRoot>
					<SplitLayout header={<PanelHeader separator={false} />}>
						<SplitCol>
							<View activePanel="main">
								<Panel id="main">
									<PanelHeader>Фонарик</PanelHeader>
									<Group header={<Header mode="secondary">Растягивание по ширине</Header>}></Group>
									<Div style={{ display: 'flex' }}>
										<Button size="s" onClick={this.btn0}></Button>
										<Button size="s" onClick={this.btn1}></Button>
										<Button size="s" onClick={this.btn2}></Button>
										<Button size="s" onClick={this.btn3}></Button>
										<Button size="s" onClick={this.btn4}></Button>
										<Button size="s" onClick={this.btn5}></Button>
										<Button size="s" onClick={this.btn6}></Button>
										<Button size="s" onClick={this.btn7}></Button>
									</Div>
								</Panel>
							</View>
						</SplitCol>
					</SplitLayout>
				</AppRoot >
			</AdaptivityProvider >
		);
	}
}

export default App;
